import React from 'react'
import { useStaticQuery, graphql } from 'gatsby'
import { FormattedMessage, injectIntl } from 'gatsby-plugin-intl'
import { Container, Jumbotron, ListGroup, Image } from 'react-bootstrap'
import Layout from '../components/layout'
import SEO from '../components/helper/seo'

import iconCall from '../assets/icon-call.svg'
import iconEmail from '../assets/icon-email.svg'
import iconLocation from '../assets/icon-location.svg'

const ContactPage = ({ intl }) => {
  const { site } = useStaticQuery(
    graphql`
      query {
        site {
          siteMetadata {
            companyEmail
            companyPhone
          }
        }
      }
    `
  )

  return (
    <Layout>
      <SEO
        lang={intl.locale}
        title={intl.formatMessage({ id: "title" })}
        keywords={[`innovation`, `television`, `streaming`, `marketing`]}
      />
      <Jumbotron fluid>
        <Container>
          <h1 className="display-2">
            <FormattedMessage id="page.contact.title" />
          </h1>
          <p className="lead"><FormattedMessage id="page.contact.lead" /></p>
        </Container>
      </Jumbotron>
      <Container>
        <ListGroup as="ul" horizontal={'sm'}>
          <ListGroup.Item as="li" className="flex-fill">
            <Image width={40} src={iconEmail} alt="Email" />
            <h2 className="h5"><FormattedMessage id="page.contact.email" /></h2>
            <a href={"mailto:" + site.siteMetadata.companyEmail} className="mr-2">
              {site.siteMetadata.companyEmail}
            </a>
            <div><FormattedMessage id="page.contact.reply" /></div>
          </ListGroup.Item>
          <ListGroup.Item as="li" className="flex-fill">
            <Image width={40} src={iconCall} alt="Tel" />
            <h2 className="h5"><FormattedMessage id="page.contact.call" /></h2>
            <a href={"tel:" + site.siteMetadata.companyPhone} className="mr-2">
              {site.siteMetadata.companyPhone}
            </a>
            <div><FormattedMessage id="page.contact.time" /></div>
          </ListGroup.Item>
          <ListGroup.Item as="li" className="flex-fill">
            <Image width={40} src={iconLocation} alt="Adr" />
            <h2 className="h5"><FormattedMessage id="page.contact.location" /></h2>
            <address>
              <FormattedMessage id="page.contact.address" />
              <div>
                <FormattedMessage id="page.contact.country" />
              </div>
            </address>
          </ListGroup.Item>
        </ListGroup>
        {/*
        <Row>
          <Col sm="6" md="4">
            <Image width={40} src={iconEmail} />
            <h5>Email</h5>
            <span>
              We reply in ~24 hours
            </span>
          </Col>
          <Col sm="6" md="4" className="mt-4 mt-sm-0">
            <Image width={40} src={iconCall} />
            <h5>Call us</h5>
            <span className="text-muted">
              9am - 6pm CEST
            </span>
          </Col>
          <Col className="mt-4 mt-md-0">
            <Image width={40} src={iconLocation} />
            <h5>Address</h5>
            <address>
              <FormattedMessage id="page.contact.address" />
            </address>
          </Col>
        </Row>
        */}
      </Container>
    </Layout>
  )
}

export default injectIntl(ContactPage)
